.grid-container {
  display: grid;
  justify-content: center;
  margin-top: 20px;
}

.grid-row {
  margin-bottom: 10px;
}

.grid-row:last-child {
  margin-bottom: 0;
}

.grid-row:after {
  content: "";
  display: block;
  clear: both;
}

.grid-cell {
  cursor: url("../img/hammer.png"), pointer;
  width: 150px;
  height: 150px;
  margin-right: 10px;
  float: left;
  border-radius: 3px;
  border: 2px solid black;
  background: blueviolet;
}

.grid-cell:last-child {
  margin-right: 0;
}

img {
  padding: 15px 15px;
}

.info {
  text-align: center;
  margin: 15px;
  font-size: 20px;
}
